import React, { useContext, useState } from 'react'
import { CSSTransition, SwitchTransition } from 'react-transition-group'
import { toast } from 'react-toastify'
import Text from 'components/Text'
import Flex from 'components/Flex'
import Box from 'components/Box'
import contractConfig from 'config/constant/contract'
import useGetUserPackCount from 'pages/Purchase/hooks/useGetUserPackCount'
import LoaderIcon from 'components/LoaderIcon'
import { packageConfig } from 'config/constant'
import { PurchaseContext } from '../../context/PurchaseContext'
import {
  StatsText,
  StyledChangeValueButton,
  StyledClassName,
  StyledClassPercentage,
  StyledClassInput,
  StyledInput,
  PreviewImage,
  PackageWrapper,
  PackIcon,
  CardWrapper,
  ItemInfo,
  ItemWrapper,
  TextWrapper,
  StyledButton,
  StyledNumberOfPackage,
  StyledClassInfoHeader,
  PreviewText,
  ProbabilityWrapper,
  PreviewWrapper,
} from '../styled'
import theme from '../styled/colors'

const Stats = () => {
  const { epic } = theme

  return (
    <ProbabilityWrapper>
      <Text color="#F3F3F3" fontSize="1.6rem" pb="20px">
        Chance to obtain each Class (%)
      </Text>
      <Flex pb="20px" alignItems="center">
        <StyledClassName>Bronze</StyledClassName>
        <StyledClassPercentage>0.00</StyledClassPercentage>
        <StyledClassInput>
          <StyledInput inputBackgroundColor={epic.inputBackgroundColor} percentage={0.0} />
        </StyledClassInput>
      </Flex>
      <Flex pb="20px">
        <StyledClassName>Silver</StyledClassName>
        <StyledClassPercentage>0.00</StyledClassPercentage>
        <StyledClassInput>
          <StyledInput inputBackgroundColor={epic.inputBackgroundColor} percentage={0.0} />
        </StyledClassInput>
      </Flex>
      <Flex pb="20px">
        <StyledClassName>Gold</StyledClassName>
        <StyledClassPercentage>70.37</StyledClassPercentage>
        <StyledClassInput>
          <StyledInput inputBackgroundColor={epic.inputBackgroundColor} percentage={70.37} />
        </StyledClassInput>
      </Flex>
      <Flex pb="20px">
        <StyledClassName>Platinum</StyledClassName>
        <StyledClassPercentage>93.60</StyledClassPercentage>
        <StyledClassInput>
          <StyledInput inputBackgroundColor={epic.inputBackgroundColor} percentage={93.6} />
        </StyledClassInput>
      </Flex>
      <Flex>
        <StyledClassName>Legendary</StyledClassName>
        <StyledClassPercentage>18.70</StyledClassPercentage>
        <StyledClassInput>
          <StyledInput inputBackgroundColor={epic.inputBackgroundColor} percentage={18.7} />
        </StyledClassInput>
      </Flex>
    </ProbabilityWrapper>
  )
}

const Preview = () => {
  return (
    <PreviewWrapper justifyContent="center">
      <PreviewImage src="/images/preview-common.png" />
    </PreviewWrapper>
  )
}

const PackageCommon = ({ onChangeValue, onHandleBuyPack, payAmount }) => {
  const {
    cyblockPresale: { epicPackageContractAddress },
  } = contractConfig
  const { state } = useContext(PurchaseContext)
  const [isPreview, setIsPreview] = useState(false)
  const { packCount: epicPackCount } = useGetUserPackCount(epicPackageContractAddress)
  const { limitBoughtPackage } = packageConfig
  const { numberOfPackage, isLoading } = state
  const { epic } = theme

  return (
    <PackageWrapper alignItems="center">
      <ItemWrapper>
        <CardWrapper cardBackground={epic.cardBackground}>
          <PackIcon src="/images/epic.png" />
          <TextWrapper>
            <Flex>
              <StyledChangeValueButton
                color="#F3F3F3"
                fontSize="6rem"
                fontWeight="600"
                onClick={() => onChangeValue('down')}
              >
                -
              </StyledChangeValueButton>
              <StyledNumberOfPackage>{numberOfPackage}</StyledNumberOfPackage>
              <StyledChangeValueButton
                color="#F3F3F3"
                fontSize="6rem"
                fontWeight="600"
                disabled={limitBoughtPackage === numberOfPackage}
                onClick={() => onChangeValue('up')}
              >
                +
              </StyledChangeValueButton>
            </Flex>
          </TextWrapper>
          <Text color="#F3F3F3" fontSize="2rem" fontWeight="600">
            3 BNB (~1000$)
          </Text>
          <StyledButton
            buttonBackgroundColor={epic.buttonBackgroundColor}
            buttonDisabled={epic.buttonDisabled}
            isLoading={isLoading}
            onClick={() => {
              if (Number(epicPackCount) === limitBoughtPackage) {
                toast.error('You already reach limit', {
                  hideProgressBar: true,
                })

                return
              }

              if (!isLoading) {
                onHandleBuyPack()
              }
            }}
          >
            {isLoading ? (
              <LoaderIcon />
            ) : (
              <Text color="#110A19" fontWeight="700">
                Buy for 3 BNB
              </Text>
            )}
          </StyledButton>
        </CardWrapper>
      </ItemWrapper>
      <ItemInfo cardBackgroundColor={epic.cardBackgroundColor}>
        <StyledClassInfoHeader>
          <PreviewText isPreview={isPreview} onClick={() => setIsPreview(true)}>
            Preview
          </PreviewText>
          <StatsText isPreview={isPreview} onClick={() => setIsPreview(false)}>
            Probability
          </StatsText>
        </StyledClassInfoHeader>
        <SwitchTransition mode="out-in">
          <CSSTransition
            key={isPreview}
            addEndListener={(node, done) => {
              node.addEventListener('transitionend', done, false)
            }}
            classNames="fade"
          >
            {isPreview ? <Preview /> : <Stats />}
          </CSSTransition>
        </SwitchTransition>
      </ItemInfo>
    </PackageWrapper>
  )
}

export default PackageCommon
