import React, { useMemo } from 'react'
import { CSSTransition } from 'react-transition-group'
import Box from 'components/Box'
import Flex from 'components/Flex'
import { PackageFrame, PackageImage, CardText, SelectedIcon } from './styles'

const CommonPackageCard = ({ data, selectedPackage, onClick }) => {
  const isSelected = useMemo(() => {
    const { type, packageIndex } = selectedPackage
    if (type === 'common' && packageIndex.includes(data)) {
      return true
    }

    return false
  }, [selectedPackage])

  return (
    <PackageFrame
      url={isSelected ? '/images/inventory-frame-common-selected.png' : '/images/inventory-frame-common.png'}
      onClick={onClick}
    >
      <CSSTransition in={isSelected} timeout={200} classNames="fade" unmountOnExit appear>
        <SelectedIcon src="/images/selector.png" />
      </CSSTransition>
      <Flex flexDirection="column" justifyContent="center" pt="25px" pb="25px" height="100%" alignItems="center">
        <PackageImage src="/images/common.png" />
        <Box width="100%" mt="20px" pl="20px" pr="20px" mb="20px" textAlign="center">
          <CardText fontSize="2.2rem" mb="10px">
            Common pack <br /> #{Number(data) + 1}
          </CardText>
          <CardText fontSize="1.4rem">ETA: few seconds</CardText>
        </Box>
      </Flex>
    </PackageFrame>
  )
}

export default CommonPackageCard
