import { useCallback } from 'react'
import axios from 'axios'
import { useWeb3React } from '@web3-react/core'
import { API_CYBALL, contractConfig } from 'config/constant'
import { unPack } from 'utils/callHelpers'
import { useCyblockPresaleContract } from './useContract'

// We will select correct contract, based on selected package
const useOpenPackage = (packageContractAddress, packType) => {
  const { account } = useWeb3React()
  const {
    cyblockPresale: { commonPackageContractAddress, rarePackageContractAddress, epicPackageContractAddress },
  } = contractConfig
  const cyblockPresaleContract = useCyblockPresaleContract(packageContractAddress)
  const handleUnpack = useCallback(
    async (packages) => {
      const { data = {} } = await axios.post(
        `${API_CYBALL}/PresaleOpenPackProof?code=${process.env.REACT_APP_AZURE_CODE}&contract=${packageContractAddress}&packType=${packType}&address=${account}`,
        {
          packsIndex: packages,
        },
      )
      const { PresaleOpenPacks } = data
      if (PresaleOpenPacks) {
        const { proofs, userPacks } = PresaleOpenPacks
        const txHash = await unPack(cyblockPresaleContract, account, userPacks, proofs)
        return txHash
      }

      return null
    },
    [account, cyblockPresaleContract],
  )

  return { onUnpack: handleUnpack }
}

export default useOpenPackage
