import { packageConfig } from 'config/constant'

export const buyCyblocPack = async (cyblockPresaleContract, quantity, amount, account, proof) => {
  const { limitBoughtPackage } = packageConfig
  return cyblockPresaleContract.methods
    .buyCyblocsPacks(quantity, limitBoughtPackage, proof)
    .send({
      from: account,
      value: amount,
    })
    .on('transactionHash', (tx) => {
      return tx.transactionHash
    })
}

export const unPack = (cyblockPresaleContract, account, packages, proofs) => {
  return cyblockPresaleContract.methods
    .unpack(packages, proofs)
    .send({
      from: account,
    })
    .on('transactionHash', (tx) => {
      return tx.transactionHash
    })
}

export const getPackageIndex = async (cyblockPresaleContract, address, startIndex, endIndex) => {
  return cyblockPresaleContract.methods.getMyPackageIndex(address, startIndex, endIndex).call()
}
