import React, { useEffect, useMemo, useState } from 'react'
import styled from 'styled-components'
import { useSelector } from 'react-redux'
import { toast } from 'react-toastify'
import queryString from 'query-string'
import { useWeb3React } from '@web3-react/core'
import { useHistory, useRouteMatch } from 'react-router-dom'
import { useQueryParam, StringParam } from 'use-query-params'
import { useModal, CardDetailModal, OpenPackModal } from 'components/Modal'
import { selectCyblocList, selectCyblocLoading, setNewCylocList, setSelectedNftId } from 'state/reducers/cybloc'
import contractConfig from 'config/constant/contract'
import Box from 'components/Box'
import Flex from 'components/Flex'
import Text from 'components/Text'
import PageLoading from 'components/PageLoading'
import useOpenPackage from 'hooks/useOpenPackage'
import differenceWith from 'lodash/differenceWith'
import isEqual from 'lodash/isEqual'
import { useAppDispatch } from 'state'
import { CommonPackageCard, RarePackageCard, EpicPackageCard } from './components/PackageCard'
import CyblocCard from './components/CyblocCard'
import NFTDetail from './NFTDetail'
import { getCyblocList } from './hooks/useGetCyblocList'

const Wrapper = styled(Box)`
  background-color: #191123;
  min-height: inherit;
`
const Row = styled(Box)`
  max-width: 1000px;
  margin: 0 auto;

  @media (max-width: 1100px) {
    max-width: 800px;
  }

  @media (max-width: 800px) {
    max-width: 80%;
  }

  @media (max-width: 576px) {
    max-width: 350px;
  }
`
const StyledHeaderContent = styled(Flex)`
  justify-content: space-between;
  align-items: center;

  @media (max-width: 768px) {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-bottom: 40px;
  }
`

const StyledHeaderText = styled(Text)`
  font-size: ${(props) => (props.isActive ? '3rem' : '1.4rem')};
  line-height: 48px;
  letter-spacing: 2px;
  font-weight: 700;
  color: #f3f3f3;
  cursor: pointer;
  padding: 30px 0px;
  opacity: ${(props) => (props.isActive ? '1' : '0.6')};
  transition: 0.3s opacity ease-out;

  @media (max-width: 600px) {
    padding: 0px;
  }
`
const OpenPackButton = styled.button`
  justify-content: center;
  align-items: center;
  display: flex;
  background-color: transparent;
  border: none;
  background-image: url('/images/open-pack-frame.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  transition: background-image 0.3s ease-in, transform 0.1s ease-in, opacity: 0.3s ease-in;
  padding: 15px 20px;
  border-radius: 2px;
  opacity: ${(props) => (props.disabled ? '0.2' : '1')};
  @media (max-width: 768px) {
    width: 100%;
  }

  &:hover {
    cursor: ${(props) => (props.disabled ? 'not-allowed' : 'pointer')};
    opacity: ${(props) => (props.disabled ? '0.2' : '1')};
  }

  &:active:enabled {
    transform: translateY(-2px);
    opacity: 80%;
  }

  &:not(:last-of-type) {
    margin-right: 20px;

    @media (max-width: 768px) {
      margin-right: 0px;
      margin-bottom: 20px;
    }
  }

  & > div {
    font-size: 1.6rem;
    color: #fefefe;
    line-height: 17.15px;
    letter-spacing: 2px;
    font-weight: 700;
  }
`
const Grid = styled(Box)`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 40px;
  row-gap: 40px;

  @media (max-width: 1100px) {
    grid-template-columns: 1fr 1fr;
  }

  @media (max-width: 580px) {
    grid-template-columns: 1fr;
  }
`

const Inventory = ({ packageData, isLoading }) => {
  const {
    cyblockPresale: { commonPackageContractAddress, rarePackageContractAddress, epicPackageContractAddress },
  } = contractConfig
  const cyblocList = useSelector(selectCyblocList)
  const [isLoadingOpenPack, setIsLoadingOpenPack] = useState(false)
  const isLoadingCyblocList = useSelector(selectCyblocLoading)
  const dispatch = useAppDispatch()
  const [tabQuery = 'packs', setTabQuery] = useQueryParam('tab', StringParam)
  const history = useHistory()
  const [selectedPackage, setSelectedPackage] = useState({
    type: null,
    packageIndex: [],
  })
  const { path } = useRouteMatch()
  const { account } = useWeb3React()
  const [onPresentOpenPackModal] = useModal(<OpenPackModal packageData={packageData} history={history} />)
  const { onUnpack: onUnPackCommonPackage } = useOpenPackage(commonPackageContractAddress, 'normal')
  const { onUnpack: onUnpackRarePackage } = useOpenPackage(rarePackageContractAddress, 'rare')
  const { onUnpack: onUnpackEpicPackage } = useOpenPackage(epicPackageContractAddress, 'epic')

  const [onPresentViewCyBlocDetailModal, onDimiss] = useModal(
    <CardDetailModal>
      <NFTDetail
        onDismiss={() => {
          onDimiss()
        }}
      />
    </CardDetailModal>,
  )

  useEffect(() => {
    dispatch(setNewCylocList([]))
  }, [])

  const { commonPackageData, rarePackageData, epicPackageData } = packageData

  const onNavigateToOpen = (packageType, packageIndex) => {
    const stringtifiedQuery = queryString.stringify(
      {
        packageIndex,
      },
      {
        arrayFormat: 'bracket',
      },
    )
    history.push(`${path}/package-opening/${packageType}?${stringtifiedQuery}`)
  }

  const onHandleSelectPackage = (packageType, index) => {
    const { type, packageIndex } = selectedPackage
    if (type === packageType) {
      if (packageIndex.includes(index)) {
        setSelectedPackage({
          ...selectedPackage,
          packageIndex: packageIndex.filter((i) => i !== index),
        })
      } else {
        setSelectedPackage({
          ...selectedPackage,
          packageIndex: [...packageIndex, index],
        })
      }
    } else {
      setSelectedPackage({
        type: packageType,
        packageIndex: [index],
      })
    }
  }

  const onHandleOpenSelectedPackage = async () => {
    try {
      setIsLoadingOpenPack(true)
      const { type, packageIndex } = selectedPackage
      let result = null

      if (type === 'common') {
        result = await onUnPackCommonPackage(packageIndex)
      }

      if (type === 'rare') {
        result = await onUnpackRarePackage(packageIndex)
      }

      if (type === 'epic') {
        result = await onUnpackEpicPackage(packageIndex)
      }

      if (result.status) {
        // Successfully open
        const data = await getCyblocList(account)
        if (data) {
          const { userCyBlocs } = data
          const newEls = differenceWith(userCyBlocs, cyblocList, isEqual)
          dispatch(setNewCylocList(newEls))
          setIsLoadingOpenPack(false)
          onNavigateToOpen(type, packageIndex)
        }
      }
    } catch (error) {
      setIsLoadingOpenPack(false)
      toast.error('Fail to open packages', {
        hideProgressBar: true,
      })
    }
  }
  return (
    <Wrapper>
      <Row>
        {isLoading || isLoadingCyblocList || isLoadingOpenPack ? (
          <PageLoading />
        ) : (
          <>
            <StyledHeaderContent>
              <Flex alignItems="baseline">
                <StyledHeaderText
                  mr="15px"
                  isActive={tabQuery === 'packs'}
                  onClick={() => {
                    setTabQuery('packs')
                  }}
                >
                  Packs
                </StyledHeaderText>
                <StyledHeaderText
                  isActive={tabQuery === 'cyblocs'}
                  onClick={() => {
                    setTabQuery('cyblocs')
                  }}
                >
                  Cyblocs
                </StyledHeaderText>
              </Flex>
              <Flex flexWrap="wrap">
                <OpenPackButton
                  disabled={selectedPackage.packageIndex.length === 0}
                  onClick={onHandleOpenSelectedPackage}
                >
                  <Text>Open selected packs</Text>
                </OpenPackButton>
                <OpenPackButton
                  onClick={onPresentOpenPackModal}
                  disabled={
                    commonPackageData.length === 0 && rarePackageData.length === 0 && epicPackageData.length === 0
                  }
                >
                  <Text>Open multiple packs</Text>
                </OpenPackButton>
              </Flex>
            </StyledHeaderContent>

            {tabQuery === 'packs' ? (
              <Grid>
                {commonPackageData.map((data) => {
                  return (
                    <CommonPackageCard
                      data={data}
                      onClick={() => onHandleSelectPackage('common', data)}
                      selectedPackage={selectedPackage}
                    />
                  )
                })}
                {rarePackageData.map((data) => {
                  return (
                    <RarePackageCard
                      data={data}
                      onClick={() => onHandleSelectPackage('rare', data)}
                      selectedPackage={selectedPackage}
                    />
                  )
                })}
                {epicPackageData.map((data) => {
                  return (
                    <EpicPackageCard
                      data={data}
                      onClick={() => onHandleSelectPackage('epic', data)}
                      selectedPackage={selectedPackage}
                    />
                  )
                })}
              </Grid>
            ) : (
              <Grid>
                {cyblocList.map(({ tokenId, cardImage }) => {
                  return (
                    <CyblocCard
                      url={cardImage}
                      onClick={() => {
                        dispatch(setSelectedNftId(tokenId))
                        onPresentViewCyBlocDetailModal()
                      }}
                    />
                  )
                })}
              </Grid>
            )}
          </>
        )}
      </Row>
    </Wrapper>
  )
}

export default Inventory
