import { useCallback } from 'react'
import axios from 'axios'
import { useWeb3React } from '@web3-react/core'
import { buyCyblocPack } from 'utils/callHelpers'
import { API_CYBALL, packageConfig } from 'config/constant'
import { useCyblockPresaleContract } from './useContract'

// We will select correct contract, based on selected package
const useBuyCyblocsPacks = (packageAddress) => {
  const { account } = useWeb3React()
  const cyblockPresaleContract = useCyblockPresaleContract(packageAddress)
  const handleBuyCyblockPack = useCallback(
    async (quantity, amount) => {
      // Get proof to buy package
      const { data } = await axios.get(`${API_CYBALL}/PresaleBuyPackProof`, {
        headers: {
          'x-functions-key': process.env.REACT_APP_AZURE_CODE,
        },
        params: {
          code: process.env.REACT_APP_AZURE_CODE,
          address: account,
          max: packageConfig.limitBoughtPackage,
          contract: packageAddress,
        },
      })
      if (data) {
        const { proof } = data
        const txHash = await buyCyblocPack(cyblockPresaleContract, quantity, amount, account, proof)
        return txHash
      }

      return null
    },
    [account, cyblockPresaleContract],
  )

  return { onBuyPack: handleBuyCyblockPack }
}

export default useBuyCyblocsPacks
